.mfui-tabs__swiper {
  font-family: inherit;
  font-size: 15px;
  line-height: 24px;
  font-weight: 500;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
}
.mfui-tabs__swiper:before,
