h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}
.mfui-paragraph {
  font-family: inherit;
  margin: 0;
  font-weight: 400;
}
.mfui-paragraph_align_left {
  text-align: left;
}
.mfui-paragraph_align_center {
  text-align: center;
}
.mfui-paragraph_align_right {
  text-align: right;
}
.mfui-paragraph_size_regular {
  font-size: 15px;
  line-height: 24px;
}
.mfui-paragraph_size_small {
  font-size: 12px;
  line-height: 16px;
}
.mfui-paragraph_has-margin {
  margin: 0 0 16px;
}
.mfui-paragraph_color_green {
  color: #00B956;
}
.mfui-paragraph_color_purple {
  color: #731982;
}
.mfui-paragraph_color_base,
.mfui-paragraph_color_clearWhite {
  color: #FFFFFF;
}
.mfui-paragraph_color_spbSky0 {
  color: #F6F6F6;
}
.mfui-paragraph_color_spbSky1 {
  color: #EDEDED;
}
.mfui-paragraph_color_spbSky2 {
  color: #D8D8D8;
}
.mfui-paragraph_color_content,
.mfui-paragraph_color_freshAsphalt {
  color: #333333;
}
.mfui-paragraph_color_fullBlack {
  color: #000000;
}
.mfui-paragraph_color_inherit {
  color: inherit;
}
