}
.mfui-text-field_icon .mfui-text-field__field {
  padding-right: 55px;
}
@media screen and (min-width: 1024px) {
  .mfui-text-field_icon.mfui-text-field_size_large .mfui-text-field__field {
    padding-right: 65px;
  }
}
.mfui-text-field_password .mfui-text-field__field {
  font: small-caption;
  font-size: 19px;
}
